<template>
  <view class="content">
    <image class="logo" src="/static/logo.png"></image>
    <view class="text-area">
      <text class="title">{{ title }}</text>
    </view>
  </view>
</template>

<script>
import { Request } from "../../request.js";
const request = new Request({
  baseUrl: "localhost:3022",
});

request.interceptors.request.use(
  (config) => {
    console.log(config, "interceptors 1");
    config.header = {
      "content-type": "application/x-www-form-urlencoded",
    };
    return Promise.reject(config);
  },
  (error) => {
    console.log(error, "interceptors error 1");
  }
);

request.interceptors.request.use(
  (config) => {
    console.log(config, "interceptors 2");

    return config;
  },
  (error) => {
    console.log(error, "interceptors error 2");
  }
);

export default {
  data() {
    return {
      title: "Hello",
    };
  },
  onLoad() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      const res = await request.request({
        url: "/",
      });
      console.log(res, "log");
    },
  },
};
</script>

<style>
.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.logo {
  height: 200rpx;
  width: 200rpx;
  margin-top: 200rpx;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50rpx;
}

.text-area {
  display: flex;
  justify-content: center;
}

.title {
  font-size: 36rpx;
  color: #8f8f94;
}
</style>
